import Vue from 'vue'
import fbi from "../../components/firebaseInit";

export default {
    name: 'viewEmployee',

  data: function () {
    return {
     employee:{},
      id: null,
      employees: [],
    }},

    components: {
      fbi
    },

    methods: {

      getCollection(){
        fbi.collection('my-base').get().then(
          querySnapshot => {
            querySnapshot.forEach(doc => {
              this.employees.push(doc.data());
            });
            console.log(this.employees, "все");
            this.getIdAndPush()
          }
        );
      },
      getIdAndPush(){
        this.id = Number(this.$route.params.note_id);
        console.log(this.id, "id");

        for(let item in this.employees){
          if(this.employees[item].note_id === this.id){
            this.employee = this.employees[item]
          }

        }
        console.log(this.employee, "Одна заметка");

      },

      deleteNote(){
           fbi.collection('my-base').where('note_id', "==", this.id).get().then(
             querySnapshot => {
               querySnapshot.forEach(doc => {
                 if(confirm("Вы уверены?")){
                   doc.ref.delete()
                   this.$router.push('/')
                 }

               });

             }
           );

      }
    },
  created() {
    this.getCollection()
  }
};



