import Vue from 'vue'
import fbi from '../../components/firebaseInit'

export default {
    name: 'dashboard',

  data: function () {
    return {
      employees: []
    }},

    components: {
      fbi
    },

    methods: {

      viewNote(item){
        let note_id = item.note_id;
        this.$router.push({name: "view", params: {note_id: note_id}});
      },
       getCollection(){
        fbi.collection('my-base').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.employees.push(doc.data());
              console.log(this.employees);
            })
            }
          );


         console.log(this.employees)
       }
    },
  created() {
    this.getCollection()
  }
};



