import Vue from 'vue'
import fbi from "../../components/firebaseInit";

export default {
    name: 'newEmployee',

  data: function () {
    return {
      opened: false,
      pre: [],
      time: "",
      categories: ["Работа",
        "Дом",
        "Развлечения",
        "Цели",
        "Учеба",
        "Программирование",
        "Планы"],
      category: "Категория",
      title: "",
      content:"",
      time2: "",
      employees:[],
      id: null
    }},

    components: {
    },

    methods: {

      saveNote(){
        fbi.collection('my-base').add(
          {
            note_id: this.id,
            note_content: this.content,
            note_date: this.time,
            note_theme: this.category,
            note_title: this.title,
          }
        );
        this.$router.push('/')
      },

      dateAndStatus(){
        this.time = new Date();

          fbi.collection('my-base').get().then(querySnapshot => {
              querySnapshot.forEach(doc => {
                this.employees.push(doc.data());
                  for(let item in this.employees){
                    this.pre.push(this.employees[item].note_id);
                    console.log(this.pre, "no sorted");
                    for(let item in this.pre){
                      for(let i = 0; i<this.pre.length-1; i++){
                        if(this.pre[i]>this.pre[i+1]){
                          let cont = this.pre[i+1];
                          this.pre[i+1] = this.pre[i];
                          this.pre[i] = cont
                        }
                      }
                      console.log(this.pre, "sorted");
                    }
                    for(let item3 in this.pre){
                      this.id = this.pre[item3] + 1;
                    }
                  }


                //console.log(this.employees);
                console.log(this.id)

              });

            }
          );






      },

      openDrop(){
        this.opened = !this.opened;
      },
      getCategory(item){
        this.category = item;
        this.opened = false
      }

    },
  created() {
      this.dateAndStatus()

  }
};



